@use "global" as *;

:root {
  --header-h-pc: rem(80);
}

.services {
  background-color: $blue;
  padding-bottom: rem(40);
  position: relative;
  --edge-shift: clamp(0px, calc((1366px - 100vw) * 0.80), 60px);
}

.services__slider-wrapper {
  overflow: hidden;
  position: relative;
  transform: translateY(rem(-54));
  width: 100%;

  @include mq(md) {
    transform: translateY(rem(-32));
  }
}

.services__slide {
  aspect-ratio: 608 / 493;
  border-radius: 20px;
  overflow: hidden;
  max-width: rem(608);

  @include mq(md) {
    max-width: rem(300);
  }
}

.services__bg-wrapper {
  margin-top: rem(68);
  overflow: hidden;

  @include mq(md) {
    margin-top: rem(40);
  }
}

.services__inner {
  position: relative;

  @include mq(md) {
    width: 85%;
  }
  @include mq(xs) {
    width: 100%;
  }
}

.services__anchor {
  display: block;
  height: 0;
  pointer-events: none;
  position: absolute;
  top: calc(-1 * var(--arch-overlap-pc, rem(130)));
  right: 0;
  left: 0;
  scroll-margin-top: var(--header-h, 0px);

  @include mq(md) {
    top: calc(-1 * var(--arch-overlap-pc, rem(500)));
  }
}

.services .section-title {
  gap: 0;
}

.services__title .section-title__jp,
.services__title .section-title__en {
  color: $white;
}

.services__text {
  color: $white;
  line-height: calc(36/16);
  margin: rem(12) auto 0;
  max-width: rem(560);
  text-align: center;
  width: 100%;
}

.services__list-items {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: rem(72);
  margin-top: rem(68);
  position: relative;

  @include mq(md) {
    gap: rem(24);
    margin-top: rem(60);
  }
}

.services__list-item {
  position: relative;
}

.services__bg {
  margin: 0 auto;
  width: min(100vw, 1366px);
}

.services .media {
  padding-top: calc((48px / 1366px) * 100vw);
  padding-right: calc((56px / 1366px) * 100vw);
  padding-bottom: calc((56px / 1366px) * 100vw);
  padding-left: calc((87px / 1366px) * 100vw);

  @include mq(md) {
    padding: rem(24);
    margin: auto;
  }
}

.services .media--reverse {
  padding-top: calc((49px / 1366px) * 100vw);
  padding-right: calc((72px / 1366px) * 100vw);
  padding-bottom: calc((54px / 1366px) * 100vw);
  padding-left: calc((72px / 1366px) * 100vw);
}

.services .media__wrapper {
  gap: calc((20px / 1366px) * 100vw);

  @include mq(md) {
    flex-direction: column;
    gap: rem(24);
  }
}

.services .media__header {
  flex-shrink: 0;
  width: rem(360);

  @include mq(md) {
    width: 100%;
  }
}

.services .media__title-main {
  margin-top: rem(16);
}

.services .media__description {
  margin-top: rem(10);
  line-height: calc(28 / 14);

  @include mq(md) {
    margin-top: rem(16);
  }
}

.services .visual-stack {
  position: relative;
  max-width: rem(500);
  top: rem(-80);
  right: rem(-100);

  @include mq(lg) {
    right: rem(-50);
  }
  @include mq(md) {
    max-width: rem(400);
    right: auto;
    top: auto;
  }
}

.services .media--reverse .visual-stack {
  right: auto;
  left: rem(-110);

  @include mq(lg) {
    left: rem(-70);
  }
  @include mq(md) {
    top: auto;
    left: auto;
  }
}

.services .visual-stack__main {
  aspect-ratio: 500 / 336;
  margin: 0 auto;
}

.services .visual-stack__sub {
  aspect-ratio: 246 / 165;
  right: -8%;
  bottom: -7%;
  width: 49.2%;

  @include mq(lg) {
    right: -2%;
    bottom: -15%;
  }
  @include mq(md) {
    right: -8%;
    bottom: -7%;
  }
}
