@use "global" as *;

.recruit {
  background: transparent;
  overflow: hidden;
  position: relative;
  width: 100%;
  --gutter: rem(16);

  @include mq(md) {
    padding-bottom: rem(130);
    --gutter: rem(10);
  }
}

.recruit__inner {
  position: relative;
  z-index: 1;

  @include mq(md) {
    width: 85%;
  }
  @include mq(xs) {
    width: 100%;
  }
}

.recruit__catch {
  gap: rem(4);
  font-weight: $bold;
  line-height: 1.2;
  text-align: center;
}

.recruit__catch span {
  display: block;
}

.recruit__catch span:first-child {
  color: $white;
  font-size: clamp(24px, calc(16.35px + 2.041vw), 32px);
}

.recruit__catch span:last-child {
  color: $white;
  font-size: clamp(28px, calc(16.52px + 3.061vw), 40px);
  margin-top: rem(14);
}

.recruit__circles {
  aspect-ratio: 1200 / 592;
  height: auto;
  margin: 40px auto 0;
  max-width: rem(1200);
  overflow: hidden;
  position: relative;
  width: 100%;

  @include mq(md) {
    aspect-ratio: auto;
    height: rem(1342);
    margin: 0;
    max-width: none;
    width: 100%;
  }
}

.recruit__bg-circle {
  background-image: url(../images/common/bg_circles.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  margin: auto;
  pointer-events: none;
  position: absolute;
  inset: 0;
  left: 50%;
  transform: translateX(-50%);
  width: rem(1200);
  z-index: 0;

  @include mq(md) {
    background-size: 100% 100%;
    height: rem(592);
    margin: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(70deg);
    transform-origin: center;
    width: rem(1200);
    will-change: transform;
  }

  @include mq(xs) {
    transform: translate(-50%, -50%) rotate(77deg);

  }
}

.recruit__circle-flex {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  gap: calc((80px / 1200px) * 100%);
  height: 100%;
  overflow-x: clip;
  padding-inline: rem(20);
  position: relative;
  width: 100%;

  @include mq(lg) {
    gap: calc((60px / 1200px) * 100%);
  }

  @include mq(md) {
    align-items: stretch;
    flex-direction: column;
    gap: 0;
    padding-inline: rem(16);
  }
}

.recruit__job-type {
  color: $white;
  max-width: rem(375);

  @include mq(md) {
    max-width: rem(350);
  }
  @include mq(sm) {
    max-width: rem(330);
  }
}

.recruit__job-type--left {
  @include mq(md) {
    align-self: flex-start;
    margin-left: 12vw;
  }
  @include mq(sm) {
    margin-left: 0;
  }
}

.recruit__job-type--right {
  @include mq(md) {
    align-self: flex-end;
    margin-right: 12vw;
  }
  @include mq(sm) {
    margin-right: 0;
  }
}

.recruit__job-image {
  aspect-ratio: 246 /140;
  margin: 0 auto;
  max-width: rem(246);
  width: 100%;
}

.recruit__job-image img {
  display: block;
  height: auto;
  object-fit: contain;
  width: 100%;
}

.recruit__job-labels {
  display: flex;
  align-items: center;
  gap: rem(8);
  justify-content: center;
  margin-top: rem(18);

  @include mq(lg) {
    margin-top: rem(4);
  }
}

.recruit__job-label {
  background-color: $white;
  border-radius: 20px;
  color: $l-blue;
  display: inline-block;
  font-weight: $bold;
  font-size: rem(16);
  padding: rem(4) rem(12);
}

.recruit__job-description {
  font-weight: $bold;
  line-height: calc(36 /16);
  margin-top: rem(18);

  @include mq(md) {
    font-size: rem(14);
    margin-top: rem(8);
  }
}

.recruit__list-items {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: rem(28);
  margin: rem(44) auto 0;
  max-width: rem(849);
  width: 100%;

  @include mq(md) {
    gap: rem(28);
    margin: 0 auto;
  }
}

.recruit .media {
  padding: rem(32) rem(32) rem(32) rem(56);

  @include mq(md) {
    padding: rem(20);
  }
}

.recruit .media__wrapper {
  gap: rem(20);

  @include mq(md) {
    align-items: start;
    flex-direction: column;
  }
}

.recruit .media__header {
  max-width: rem(339);
  padding-top: rem(40);

  @include mq(md) {
    max-width: 100%;
    padding-top: 0;
  }
}

.recruit .media__title-main {
  line-height: calc(50 / 32);
  margin-top: rem(12);

  @include mq(md) {
    margin-top: rem(4);
  }
}

.recruit .media__description {
  line-height: 2;
  margin-top: rem(22);

  @include mq(md) {
    margin-top: rem(16);
  }
}

.recruit .visual-stack {
  margin-left: auto;
  max-width: rem(402);
  position: relative;

  @include mq(md) {
    max-width: rem(500);
  }
}

.recruit .visual-stack__main {
  aspect-ratio: 402 / 356;
}

.recruit .visual-stack__sub {
  aspect-ratio: 198 / 132;
  position: absolute;
  right: -22%;
  bottom: -12%;
  width: 49.25%;

  @include mq(md) {
    display: none;
  }
}

.recruit__content {
  margin: rem(83) auto 0;

  @include mq(md) {
    margin: rem(60) auto 0;
  }
}

.recruit__flex {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: rem(50);

  @include mq(md) {
    flex-direction: column;
    gap: rem(32);
  }
}

.recruit__image {
  aspect-ratio: 442 / 425;
  max-width: rem(442);
  width: 100%;
}

.recruit__image img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.recruit__text {
  color: $white;
  line-height: calc(36/16);
  width: min(rem(423), 100%);

  @include mq(md) {
    font-size: rem(14);
  }
}

.recruit__button {
  margin-top: rem(47);
  text-align: center;
}

.recruit__button .button {
  align-items: center;
  justify-content: flex-start;
  font-size: clamp(18px, calc(16.706px + 0.404vw), 23.5px);
  line-height: 1;
  max-width: rem(413);
  padding: rem(20) rem(32) rem(22) rem(41);

  @include mq(md) {
    max-width: rem(360);
    padding: rem(21) rem(28);
  }
}

.recruit__button .button--forward::before {
  right: 6%;
}

.recruit__button .button--forward::after {
  right: 9%;
}

.recruit .bg-image--gear {
  right: rem(-232);
  bottom: rem(-448);
  z-index: -1;

  @include mq(md) {
    right: rem(-152);
    bottom: rem(-272);
  }
}
