@use "global" as *;

.swiper {
  width: 100%;
  overflow: hidden;
}

.swiper-wrapper {
  display: flex;
  transition-timing-function: linear !important;
}


.swiper-slide img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.js-fv-slider .swiper-wrapper {
  align-items: flex-start;
}


.js-fv-slider .swiper-slide img {
  width: 100%;
  height: auto !important;
  object-fit: cover;
  object-position: center;
}
