@use "global" as *;

.fv {
  min-height: 100svh;
  margin: 0 auto;
  max-width: rem(1600);
  overflow: hidden;
  padding-top: rem(80);
  position: relative;

  @include mq(md) {
    padding-top: rem(70);
  }
}

.fv .bg-image--gear {
  height: auto;
  max-width: rem(849);
  opacity: 0.28;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;

  @include mq(md) {
    max-width: rem(600);
  }
}

.fv__slider-wrapper {
  height: calc(100svh - rem(80));
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  padding-top: rem(15);
  position: relative;
  width: 100vw;
  z-index: 2;
}

.fv__slider {
  min-height: 60svh;
  width: 100%;
  overflow-x: hidden;

  @include mq(md) {
    height: 100svh;
  }
}

.fv__slide {
  aspect-ratio: 391 / 424;
  border-radius: 30px;
  height: auto;
  margin: 0;
  max-width: rem(391);
  overflow: hidden;

  @include mq(md) {
    max-width: rem(280);
  }
}

.fv__slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fv__slide.fv__slide--top {
  margin-top: 0;
}

.fv__slide.fv__slide--center {
  margin-top: rem(55);

}

.fv__slide.fv__slide--bottom {
  margin-top: rem(77);
}

.fv__content {
  max-width: rem(650);
  position: absolute;
  left: 9.5%;
  top: 48.5%;
  width: 100%;
  z-index: 10;

  @include mq(md) {
    margin: 0 auto;
    padding-inline: rem(30);
    top: 42%;
    left: 0;
  }
  @include mq(xs) {
    top: 44%;
  }
}

.fv__message {
  background-color: $white;
  background-clip: padding-box;
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
  display: inline;
  font-size: rem(61);
  font-weight: $bold;
  line-break: strict;
  line-height: 1.55;
  padding-inline: rem(20);


  @include mq(md) {
    line-height: 2;
    font-size: rem(30);
    padding-inline: rem(8);
  }
}

.fv__punct {
  letter-spacing: -0.5em;
}

.fv__text {
  color: $white;
  font-size: rem(28);
  line-height: calc(44/28);
  margin-top: rem(14);

  @include mq(md) {
    font-size: rem(20);
  }
  @include mq(xs) {
    font-size: rem(16);
    text-shadow: 1px 1px 1px rgba($color: #000, $alpha: 0.5);
  }
}

.fv__button {
  margin-top: rem(12);
}

.fv__button .button {
  justify-content: space-between;
  line-height: 1;
  max-width: rem(413);
  padding: rem(23) rem(54);

  @include mq(md) {
    max-width: rem(340);
    padding: rem(23) rem(30);
  }
  @include mq(xs) {
    font-size: rem(22);
  }
}

.fv__button .button--forward::before {
  top: 24%;
  right: 13%;
  transform: translateY(-50%);

  @include mq(md) {
    top: 33%;
    right: 9%;
  }
}

.fv__button .button--forward::after {
  top: 39%;
  right: 16%;

  @include mq(md) {
    top: 44%;
    right: 12%;
  }
}

.fv__button .button--forward::before,
.fv__button .button--forward::after {
  position: absolute;
  transition: transform .25s ease;
  transform: translateX(0);
  will-change: transform;
}

.fv__button .button--forward:hover::before,
.fv__button .button--forward:hover::after {
  transform: translateX(rem(6));
}


.fv__search {
  position: fixed;
  bottom: 0;
  right: max(0px, calc((100vw - 1600px) / 2));
  z-index: 200;
}

.fv__search:hover .button--fv-search {
  background-image: linear-gradient($white, $white);
  box-shadow: inset 0 0 0 2px #FF860D;
  opacity: 1;
  transition: background-image 0.3s ease;
}

.fv__search:hover .button--fv-search::before {
  background-image: url("../images/common/orange_search.svg");
  transition: background-image 0.3s ease;
}
