@use "global" as *;

.media {
  background-color: $white;
  border-radius: 40px;
  position: relative;
  z-index: 2;
}

.media__wrapper {
  display: flex;
  align-items: flex-start;
}

.media--reverse .media__wrapper {
  flex-direction: row-reverse;
}

.media__title-sub {
  color: $blue;
  font-size: rem(15);
  font-weight: $bold;
  line-height: 1;

  @include mq(md) {
    font-size: rem(14);
  }
}

.media__title-main {
  color: $black;
  font-size: clamp(24px, calc(16.347px + 2.041vw), 32px);
  font-weight: $bold;
  line-height: calc(48 / 32);
  margin-top: rem(23);

  @include mq(md) {
    margin-top: rem(8);
  }
}

.media__description {
  color: $l-black;
  font-size: rem(16);
  line-height: calc(36 / 16);

  @include mq(md) {
    font-size: rem(14);
    line-height: 2;
  }
}

.media__visual {
  position: relative;

  @include mq(md) {
    margin: 0 auto;
  }
}

.media__visual img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.media__features {
  margin-top: rem(44);
}

.media__list-items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: rem(24);

  @include mq(lg) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include mq(md) {
    gap: rem(30);
    grid-template-columns: repeat(1, 1fr);
  }
}
