@use "global" as *;

.form__wrapper {
  border-bottom: 1px solid $frm-gray;
  display: flex;
  align-items: center;
  gap: rem(34);
  padding-bottom: rem(16);

  @include mq(md) {
    align-items: start;
    gap: rem(6);
    flex-direction: column;
    margin: 0 auto;
  }
}

.form__wrapper:last-child {
  border-bottom: none;
}


.form__wrapper+.form__wrapper {
  margin-top: rem(16);
}

.form__label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: rem(18);
  line-height: calc(32 / 18);
  max-width: rem(198);
  width: 100%;

  @include mq(md) {
    gap: rem(10);
    justify-content: initial;
  }
}

.form__label span {
  padding: rem(2) rem(8);
  display: inline-block;
  font-size: rem(12);
  color: $white;
  background-color: $red;

  @include mq(md) {
    padding: 0 rem(8);
  }
}

.form__input {
  max-width: rem(548);
  width: 100%;

  @include mq(md) {
    max-width: 100%;
  }
}

.form__input input,
.form__input textarea {
  border: 1px solid $frm-gray;
  border-radius: 5px;
  display: inline-block;
  line-height: calc(32 / 16);
  padding: rem(11) rem(20);
  width: 100%;

  @include mq(md) {
    padding: rem(6) rem(12);
  }
}

.form__input textarea {
  resize: none;
  min-height: rem(200);
}

.form__input input:focus {
  border: 1px solid $l-blue;
  box-shadow: 0 0 0 2px rgba($l-blue, 0.3);
  outline: none;
}


.form__privacy {
  border: 1px solid $frm-gray;
  border-radius: 8px;
  font-size: rem(14);
  height: rem(240);
  line-height: calc(26 /14);
  max-width: rem(600);
  margin: rem(41) auto 0;
  overflow-y: scroll;
  padding: rem(10) rem(20);
  width: 100%;

  @include mq(md) {
    margin: rem(20) auto 0;
    padding: rem(8) rem(12);
  }
}

.form__checkbox {
  margin: rem(21) auto 0;
  text-align: center;
}

.form__checkbox label {
  display: block;
}


.form__checkbox input {
  display: none;
}

.form__checkbox input+span {
  cursor: pointer;
  display: inline-block;
  font-size: rem(16);
  line-height: 1;
  padding: 0 0 0 rem(40);
  position: relative;

  @include mq(xs) {
    font-size: rem(14);
  }
}

.form__checkbox input+span::before {
  content: "";
  background: #fff;
  border: 1px solid $frm-gray;
  border-radius: 4px;
  display: block;
  height: rem(24);
  position: absolute;
  top: -13%;
  left: 0;
  width: rem(24);

  @include mq(md) {
    top: -36%;
  }
}

.form__checkbox input+span::after {
  content: "";
  margin-top: rem(-2);
  position: absolute;
  top: 20%;
  left: 2%;
  transform: rotate(-45deg);
  display: block;
  width: rem(15);
  height: rem(8);
  border-bottom: rem(3) solid $frm-blue;
  border-left: rem(3) solid $frm-blue;
  transition: .3s;
  opacity: 0;
}

.form__checkbox input:checked+span::after {
  opacity: 1;
}

.form__button {
  margin-top: rem(41);
  text-align: center;

  @include mq(md) {
    margin-top: rem(24);
  }
}

.form__button .button {
  line-height: calc(32 / 24);
  max-width: rem(337);
  padding: rem(15);
}

.form__button .button--forward::before {
  background-color: $white;
  content: "";
  top: 20%;
  right: 8%;

  @include mq(md) {
    right: 10%;
    top: 29%;
  }
}

.form__button .button--forward::after {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 8px solid $black;
  top: 37%;
  right: 12%;

  @include mq(md) {
    top: 39%;
    right: 12.5%;
  }
}

.form__button .wpcf7-spinner {
  display: none !important;
}

.form__button .wpcf7-submit.is-disabled,
.form__button .wpcf7-submit:disabled {
  opacity: .5;
  pointer-events: none;
  cursor: not-allowed;
}
