@use "global" as *;

.footer {
  background-color: $blue;
  color: $white;
  flex-shrink: 0;
  overflow: visible;
  position: relative;
  z-index: 10;
}

.footer__curve {
  background-image: url(../images/common/blue_curve.webp);
  transform: translateY(-100%) scale(-1);
}

.footer__inner {
  position: relative;
  transform: translateY(rem(-10));
  z-index: 10;

  @include mq(lg) {
    transform: translateY(0);
    margin-top: rem(20);
  }

  @include mq(md) {
    margin-top: rem(40);
  }
}

.footer__flex {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 2;

  @include mq(md) {
    flex-direction: column;
    align-items: center;
    margin-top: 0;
  }
}

.footer__info {
  @include mq(md) {
    text-align: center;
  }
}

.footer__logo {
  aspect-ratio: 195 / 87;
  display: block;
  height: auto;
  max-width: rem(195);
  width: 100%;
}

.footer__logo img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.footer__address {
  font-size: rem(12);
  font-style: normal;
  line-height: calc(24 / 12);
  margin-top: rem(36);
  text-decoration: none;

  @include mq(md) {
    margin-top: rem(16);
  }
}

.footer__links {
  margin-left: rem(89);

  @include mq(md) {
    margin-left: 0;
    margin-top: rem(24);
  }
}

.footer__nav-links .nav__items {
  flex-wrap: wrap;
  gap: rem(4) rem(44);
  font-size: rem(14);

  @include mq(md) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
    gap: rem(16) rem(80);
  }
}

.footer .nav__item a {
  padding: rem(10) 0;
}


.footer__ex-links-list {
  margin-top: rem(22);
  display: flex;
  flex-direction: column;
  gap: rem(5);

  @include mq(md) {
    align-items: center;
    margin-top: rem(16);
  }
}

.footer__ex-links-item a {
  display: inline-flex;
  align-items: center;
  gap: rem(9);
  font-family: $english-font-family;
  font-size: rem(12);
  line-height: calc(26/12);
  letter-spacing: 0.05em;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
}

.footer__ex-links-item:hover a {
  opacity: 0.7;
}

.footer__ex-link::after {
  content: "";
  background-image: url("../images/common/ex-link.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: inline-block;
  height: rem(10);
  width: rem(10);

}

.footer__buttons {
  display: inline-flex;
  flex-direction: column;
  flex-shrink: 0;
  gap: rem(16);
  margin-left: calc((47 / 1366) * 100vw);

  @include mq(md) {
    margin: rem(34) 0 0;
    max-width: 100%;
  }
}

.footer__buttons .button {
  width: rem(283);
}

.footer__buttons .button--contact {
  padding: rem(16) rem(36);
}

.footer__buttons .button--search {
  padding: rem(10) rem(36) rem(9);
}

.footer__bg.bg-image--gear {
  right: -10%;
  bottom: -60%;
  z-index: 0;

  @include mq(md) {
    right: -30%;
    bottom: -20%;
  }
}

.footer__copyright {
  font-size: rem(12);
  font-family: $english-font-family;
  line-height: calc(26/12);
  letter-spacing: 0.05em;
  margin-top: rem(80);
  text-align: center;
}

.footer__bg-wrapper {
  overflow: hidden;
  pointer-events: none;
  position: absolute;
  inset: 0;
  z-index: 0;
}

.footer__bg-inner {
  height: 100%;
  margin: 0 auto;
  position: relative;
  width: min(100svw, 1366px);
}

.footer__bg.bg-image--gear {
  bottom: -65%;
  left: 50%;
  transform: translateX(calc(-50% + 110%));
  width: 100%;
  max-width: rem(507);

  @include mq(lg) {
    transform: translateX(calc(-50% + 90%));
  }
  @include mq(md) {
    bottom: -10%;
    transform: translateX(calc(-50% + 60%));
    width: 100%;
    max-width: rem(400);
  }
  @include mq(xs) {
    transform: translateX(calc(-50% + 40%));
  }
}
