@use "global" as *;

.corporate {
  background-color: $bg-black;
  position: relative;
  z-index: 0;
}

.corporate__anchor {
  display: block;
  height: 0;
  pointer-events: none;
  position: absolute;
  top: calc(-1 * var(--arch-overlap-pc, rem(80)));
  right: 0;
  left: 0;
  scroll-margin-top: var(--header-h, 0px);
}

.corporate__inner {
  @include mq(md) {
    width: 85%;
  }
  @include mq(xs) {
    width: 100%;
  }
}

.corporate__title .section-title__jp {
  color: $white;
}

.corporate__list-items {
  color: $white;
  font-size: rem(20);
  line-height: calc(36 / 20);
  margin-top: rem(56);

  @include mq(md) {
    font-size: rem(16);
    margin: rem(60) auto;
  }
}

.corporate__list-item {
  border-bottom: 1px solid $bdr-gray;
  display: flex;
  gap: 5.816%;
  font-weight: $bold;
  padding: rem(37) rem(10) rem(33);

  @include mq(md) {
    flex-direction: column;
    gap: rem(18);
  }
}

.corporate__list-item:first-child {
  padding-top: 0;
}


.corporate__list-item dt {
  width: 23.979%;

  @include mq(md) {
    width: 100%;
  }
}

.corporate__list-item dd {
  width: 67.551%;

  @include mq(md) {
    font-size: rem(14);
    width: 100%;
  }
}

.corporate__list-definition {
  text-transform: uppercase;
}
