@use "global" as *;

.section-title {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: rem(6);
  text-align: center;
}

.section-title__jp {
  color: $black;
  font-size: clamp(28px, calc(16.52px + 3.061vw), 40px);
  font-weight: $bold;
  line-height: 1.2;
}

.section-title__en {
  color: $l-blue;
  font-family: $english-font-family;
  font-weight: $bold;
  font-size: rem(16);
  text-transform: capitalize;

  @include mq(md) {
    font-size: rem(14);
  }
}
