@use "global" as *;

.contact {
  overflow: hidden;
}

.contact__header {
  position: relative;
  z-index: 1;
}

.contact__title .section-title__jp,
.contact__title .section-title__en {
  color: $white;
}

.contact__bg.bg-image--wave {
  top: -20%;
  left: -24%;
  z-index: -1;

  @include mq(md) {
    top: -10%;
    left: 50%;
    transform: translateX(-50%);
  }
}

.contact__text {
  color: $white;
  line-height: calc(32/16);
  margin: rem(92) auto 0;
  max-width: rem(704);
  text-align: center;
  width: 100%;

  @include mq(md) {
    margin-top: rem(32);
  }
}

.contact form {
  background-color: $white;
  border-radius: 20px;
  margin: rem(53) auto 0;
  max-width: rem(900);
  padding: rem(73) rem(60) rem(79);
  width: 100%;

  @include mq(md) {
    margin-top: rem(40);
    padding: rem(30) rem(20);
  }
}

.contact__button {
  margin-top: rem(83);
  text-align: center;

  @include mq(md) {
    margin-top: rem(40);
  }
}

.contact__button .button--forward {
  line-height: calc(32 / 24);
  max-width: rem(337);
  padding: rem(15);
}

.contact__button .button--forward::before {
  background-color: $white;
  top: 20%;
  right: 8%;

  @include mq(md) {
    right: 10%;
    top: 29%;
  }
}

.contact__button .button--forward::after {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 8px solid $black;
  top: 37%;
  right: 12%;

  @include mq(md) {
    top: 39%;
    right: 12.5%;
  }
}
