@use "global" as *;

:root {
  --header-h-pc: rem(80);
}

.about {
  background-color: $white;
  overflow: visible;
  position: relative;
}

.about__curve.bg-curve {
  aspect-ratio: 1366 / 152;
  background-image: url(../images/common/white_curve.webp);
  background-size: 101% 101%;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  transform: translateY(-99%);
  z-index: 1;
}


.about__anchor {
  display: block;
  height: 0;
  pointer-events: none;
  position: absolute;
  top: calc(-1 * var(--arch-overlap-pc, rem(150)));
  right: 0;
  left: 0;
  scroll-margin-top: var(--header-h, 0px);

  @include mq(md) {
    top: calc(-1 * var(--arch-overlap-pc, rem(120)));

  }
}

.about__bg-wrapper {
  margin: auto 0;
  overflow-x: hidden;
  padding-bottom: rem(189);
  z-index: 1;

  @include mq(md) {
    padding-bottom: rem(250);
  }
}

.about__inner {
  padding-top: rem(50);
  z-index: 1;

  @include mq(md) {
    width: 85%;
  }

  @include mq(xs) {
    width: 100%;
  }
}

.about__header {
  padding-inline: rem(20);
  position: relative;
  transform: translateY(rem(-45));
  z-index: 2;

  @include mq(md) {
    padding-inline: rem(16);
    transform: translateY(0);
  }
}

.about .section-title__en span {
  text-transform: lowercase;
}

.about .section-title__en,
.about .media__title-sub {
  color: $l-blue;
}

.about__list-items {
  display: grid;
  gap: rem(110);
  grid-template-columns: repeat(1, 1fr);

  @include mq(md) {
    gap: rem(140);
    margin: rem(60) auto 0;
  }
}

.about__list-item {
  position: relative;
}

.about .bg-image--about {
  top: -10%;
  right: -27%;

  @include mq(md) {
    top: 90%;
    right: 38%;
  }
}

.about .media--reverse .bg-image--about {
  top: 18%;
  right: 0;
  left: -34%;

  @include mq(md) {
    top: 90%;
    left: 37%;
  }
}

.about .media {
  border-radius: 0;
  padding-inline: rem(33);

  @include mq(lg) {
    padding-inline: 0;
  }
}

.about .media__wrapper {
  align-items: flex-start;
  gap: clamp(24px, calc(15.74px + 2.58vw), 51px);

  @include mq(lg) {
    gap: clamp(18px, calc(-3.65px + 1.61vw), 51px);

    @include mq(md) {
      flex-direction: column;
    }
  }
}

.about .media--reverse .media__wrapper {
  flex-direction: row-reverse;

  @include mq(md) {
    flex-direction: column;
  }
}


.about .media__header {
  width: min(rem(384), 41.16%);

  @include mq(md) {
    width: 100%;
  }
}

.about .media__title-main {
  line-height: 1.85;
  margin-top: rem(14);

  @include mq(md) {
    margin-top: rem(8);
  }
}

.about .media__title-sub {
  font-family: $english-font-family;
  font-size: rem(20);
  position: relative;
  padding-left: rem(40);

  @include mq(md) {
    font-size: rem(18);
  }
}

.about .media__title-sub::before {
  content: "";
  aspect-ratio: 63 / 50;
  background-image: url(../images/common/icon_gears.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  height: auto;
  max-width: rem(63);
  position: absolute;
  top: rem(-30);
  left: rem(-10);
  width: 100%;
}

.about .media__description {
  margin-top: rem(10);

  @include mq(md) {
    margin-top: rem(20);
  }
}

.about .visual-stack {
  position: relative;
  width: min(rem(397), 100%);

  @include mq(md) {
    aspect-ratio: 397 / 266;
    margin: 0 auto;
    width: min(rem(380), 100%);
  }
}

.about .media--reverse .visual-stack {
  margin-left: auto;
  width: min(rem(370), 100%);

  @include mq(md) {
    aspect-ratio: 370 / 266;
    margin: 0 auto;
    width: min(rem(360), 100%);
    z-index: 1;
  }
}

.about .visual-stack__main {
  aspect-ratio: 397 / 266;
}

.about .media--reverse .visual-stack__main {
  aspect-ratio: 370 / 266;
}

.about .visual-stack__sub {
  aspect-ratio: 220 / 173;
  position: absolute;
  right: -24%;
  bottom: -18%;
  width: 55.42%;

  @include mq(md) {
    width: 50%;
    right: -16%;
    bottom: -22%;
  }

  @include mq(md) {
    width: 45%;
    right: -7%;
  }

  @include mq(xs) {
    right: -4%;
  }
}

.about .media--reverse .visual-stack__sub--small {
  aspect-ratio: 143 / 173;
  right: auto;
  bottom: -34%;
  left: -30%;
  width: min(rem(143), 38.65%);

  @include mq(lg) {
    left: -20%;
  }

  @include mq(md) {
    bottom: -20%;
    left: -7%;
  }

  @include mq(md) {
    left: -4%;
  }
}

.about .media--reverse .visual-stack__sub--large {
  aspect-ratio: 264 / 173;
  right: auto;
  bottom: -43%;
  left: -44%;
  width: 71.35%;

  @include mq(lg) {
    left: -30%;
  }

  @include mq(md) {
    bottom: -24%;
    left: -9%;
    width: 54%;
  }

  @include mq(xs) {
    bottom: -28%;
    left: -3%;
    width: 49%;
  }
}
