@use "global" as *;

.bg-curve {
  aspect-ratio: 1366 / 144;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: block;
  height: auto;
  position: absolute;
  pointer-events: none;
  top: 0;
  right: 0;
  left: 0;
  width: calc(100vw - (100vw - 100%));
  z-index: 0;
}
