@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1000px;
// ※キャンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 20px;
$padding-sp: 16px;

////////////////////
// フォント設定
////////////////////
// フォントウェイト
$regular: 300;
$medium: 500;
$bold: 700;

// フォントファミリー
$base-font-family: "Noto Sans JP", sans-serif;
$english-font-family: "Jost", sans-serif;


////////////////////
// 色の指定
////////////////////
$black: #152234;
$l-black: #162133;
$bg-black: #191818;
$bdr-black: #707070;
$white: #fff;
$bg-gray: #F4F5F5;
$bdr-gray: #E4E7ED;
$frm-gray: #E0E0E0;
$frm-blue: #3588CA;
$l-blue: #007CC8;
$blue: #004099;
$btn-blue: #004098;
$skyblue: #DCF1FA;
$red: #D00000;


body {
  display: flex;
  flex-direction: column;
  font-family: $base-font-family;
  font-weight: $medium;
  min-height: 100vh;
}

html.u-lock,
html.u-lock body {
  overflow: hidden;
}

html {
  background: linear-gradient(to bottom, #1d74d5, #003c99);
}

main {
  flex: 1;
  position: relative;
}

@media (hover: hover) and (pointer: fine) {
    a:hover {
    opacity: 0.7;
  }
}
