@use "global" as *;

.header {
  height: rem(80);
  background-color: rgba($color: #0A2E58, $alpha: 0.5);
}

.header__inner {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__inner.inner {
  height: inherit;
  max-width: rem(1600);
  padding-inline: rem(42) rem(40);
  width: 100%;
}

.header__logo {
  max-width: rem(160);
  width: 100%;
  height: inherit;
}

.header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
}

.header__logo figure {
  aspect-ratio: 160 / 33;
  height: inherit;
  width: 100%;
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__nav {
  height: inherit;

  @include mq(md) {
    display: none;
  }
}

.header .nav__item:last-child {
  margin-left: rem(34);
}

.header .nav__item:last-child a {
  font-family: $base-font-family;
  max-width: rem(176);
  padding: rem(8) rem(20);
}


.header__hamburger {
  display: none;

  @include mq(md) {
    cursor: pointer;
    display: block;
    height: inherit;
    position: relative;
    z-index: 999;
    transition: .3s;
    width: rem(40);
  }
}

.header__hamburger.is-open {
  background-color: transparent;
}

.header__hamburger span {
  background-color: #fff;
  display: block;
  height: 1px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  transition: .5s;
  width: 100%;
}

.header__hamburger span:nth-of-type(1) {
  top: rem(-8);
}

.header__hamburger span:nth-of-type(2) {
  top: 0;
}

.header__hamburger span:nth-of-type(3) {
  top: rem(8);
}

.header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}

.header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.header__hamburger.is-open span:nth-of-type(3) {
  top: rem(-2);
  transform: translateX(-50%) rotate(-45deg);
}

.header__drawer {
  display: none;

  @include mq(md) {
    background: linear-gradient(to bottom, #4B92E5, #004099);
    height: 100dvh;
    overflow-y: scroll;
    overscroll-behavior: contain;
    -webkit-overflow-scrolling: touch;
    padding: rem(80) 0;
    position: fixed;
    inset: 0;
    scrollbar-width: none;
    width: 100%;
    z-index: 900;

    &.is-open {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      place-items: center;
    }
  }
}

.header__drawer::-webkit-scrollbar {
  display: none;
}

.header__drawer-nav {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.header__drawer-item {
  text-align: center;
}

.header__drawer-item .nav__label {
  padding: rem(20) 0;
  display: block;
  font-size: rem(28);
  font-family: $english-font-family;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.05em;
  color: #fff;
  text-transform: capitalize;
}

.header__drawer-item--contact {
  margin-top: rem(24);
  padding: 0 rem(30);
}

.header__drawer-item .button--contact,
.header__drawer-item .button--search {
  display: inline-flex;
  font-size: rem(24);
  max-width: rem(280);
  width: 100%;
}

.header__drawer-item .button--contact {
  padding: rem(20) 0;
}

.header__drawer-item--contact .button--contact::before {
  @include mq(md) {
    height: rem(24);
    width: rem(24);
  }
}

.header__drawer-item .button--search {
  margin-top: rem(24);
  padding: rem(14) rem(36);
}
