@use "global" as *;

.bg-image {
  isolation: isolate;
  pointer-events: none;
  position: absolute;
  z-index: 0;
}

.bg-image::before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  inset: 0;
  position: absolute;
}


// 歯車のあしらい
.bg-image--gear {
  aspect-ratio: 1/ 1;
  max-width: rem(507);
  width: 100%;

  @include mq(md) {
    max-width: rem(400);
  }
}

.bg-image--gear::before {
  background-image: url(../images/common/bg_gear.webp);
  filter: grayscale(100%) brightness(2);
  inset: 0;
  opacity: 0.32;
  z-index: 1;

  /* 回転アニメーション */
  animation: gear-rotate 40s linear infinite;
}

@keyframes gear-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


// 波状のあしらい
.bg-image--about {
  aspect-ratio: 799 / 304;
  max-width: rem(799);
  width: 100%;

  @include mq(md) {
    max-width: rem(400);
  }
}

.bg-image--about::before {
  background-image: url(../images/common/bg_wave-about.webp);
  inset: 0;
}


// リクルート・サービスセクションの背景画像の共通項
.bg-image--filter {
  aspect-ratio: 1651 / 645;
  top: rem(128);
  left: 50%;
  transform: translateX(-50%);
  width: min(100vw, 1366px);
}

// overlayでフィルターとして使用
.bg-image--filter::before {
  background-blend-mode: overlay;
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  background-size: cover, cover;
}

// リクルートセクションの背景画像
.bg-image--recruit::before {
  background-image: url(../images/common/bg_recruit.webp);
}

// サービスセクション・コミュニケーションの背景画像
.bg-image--communication::before {
  background-image: url(../images/common/bg_communication.webp), linear-gradient($blue, $blue);
}

// サービスセクション・ネットワーク構築の背景画像
.bg-image--network::before {
  background-image: url(../images/common/bg_network.webp), linear-gradient($blue, $blue);
}

// サービスセクション・ソフトウェア開発の背景画像
.bg-image--software::before {
  background-blend-mode: screen;
  background-image: url(../images/common/bg_software.webp), linear-gradient($blue, $blue);
  filter: opacity(40%);
}


// 波状のあしらい
.bg-image--color-wave {
  position: absolute;
  pointer-events: none;
  z-index: 0;
}

.bg-image--color-wave::before,
.bg-image--color-wave::after {
  content: "";
  background: url(../images/common/bg_wave.webp) center / cover no-repeat;
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: 0;
}

// 波状のあしらい
.bg-image--wave {
  aspect-ratio: 607 / 164;
  height: auto;
  inset: 0;
  max-width: rem(607);
  position: absolute;
  width: 100%;
}

.bg-image--wave::before {
  background-image: url(../images/common/bg_wave.webp);
  inset: 0;
  z-index: -1;
}
