@use "global" as *;

.feature__icon {
  aspect-ratio: 1 / 1;
  height: auto;
  margin: 0 auto;
  max-width: rem(113);
  width: 100%;

  @include mq(md) {
    max-width: rem(70);
  }
}

.feature__icon img {
  height: 100%;
  object-fit: contain;
  width: 100%;
}


.feature__title {
  color: $blue;
  font-weight: $bold;
  font-size: rem(15);
  line-height: calc(28/15);
  margin-top: rem(12);
  text-align: center;

  @include mq(md) {
    margin-top: rem(8);
  }
}

.feature__text {
  font-size: rem(14);
  line-height: calc(28/14);
  margin-top: rem(12);

  @include mq(md) {
    margin-top: rem(8);
  }
}
