@use "global" as *;

.nav__items {
  display: flex;
  align-items: center;
  height: inherit;
}

.nav__item {
  height: inherit;
}

.nav__item--contact {
  display: flex;
  align-items: center;
  height: initial;
}

.nav__item a {
  color: $white;
  display: flex;
  align-items: center;
  font-family: $english-font-family;
  height: inherit;
  line-height: calc(26 / 14);
  padding-inline: rem(22);
  position: relative;
}

.nav__item:first-child a {
  padding-left: 0;
}

/* すべてのナビ共通：下線アニメーション */
.nav__label {
  position: relative;
  display: inline-block;
  text-transform: capitalize;
}

.nav__label::after {
  content: "";
  background-color: $white;
  border-radius: rem(3);
  height: rem(2);
  position: absolute;
  left: 0;
  bottom: -0.2em;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.25s ease;
  width: 100%;
}

.nav__item a:hover .nav__label::after,
.nav__item a:focus-visible .nav__label::after {
  transform: scaleX(1);
}

.nav__sub {
  text-transform: lowercase;
}
