@use "global" as *;

.button {
  border-radius: rem(50);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: rem(16);
  font-weight: $bold;
  line-height: calc(20 / 16);
  position: relative;
  transition: all 0.3s ease;
  width: 100%;
}

.button:hover {
  opacity: 1;
}

.button--navy {
  background-color: $black;
  color: $white;
}

.button--contact {
  justify-content: center;
  gap: rem(8);
}

.button:hover.button--contact {
  background-color: $white;
  color: $black;
}

.button.button--contact::before {
  content: "";
  background-image: url(../images/common/white_mail.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: $white;
  display: inline-block;
  height: rem(24);
  width: rem(24);
  transition: all 0.3s;
}

.button:hover.button--contact::before {
  background-image: url(../images/common/black_mail.svg);
  background-size: cover;
}

/* MAYA SEARCHの追従ボタン */
.button--fv-search {
  background-image: linear-gradient(to bottom, #E0B77D, #FF860D);
  border-radius: 0;
  border-top-left-radius: 30px;
  box-shadow: inset 0 0 0 2px transparent;
  color: $white;
  display: block;
  height: rem(106);
  padding: rem(19) rem(24) rem(21);
  position: relative;
  transition: background-image 0.3s ease, box-shadow .3s ease;
  width: clamp(210px, calc(164.08px + 12.245vw), 258px);

  @include mq(md) {
    height: rem(86);
    padding: rem(10) rem(20);
  }
}

.button--fv-search::before {
  content: "";
  aspect-ratio: 164 / 67;
  background-image: url(../images/common/search.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  display: block;
  height: auto;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: background-image 0.3s ease;
  width: clamp(134px, calc(105.30px + 7.653vw), 164px);
}

.button--white {
  background-color: $white;
  color: $btn-blue;
}

//丸+三角形のアイコン付きボタン
.button--forward {
  font-size: rem(24);
  font-weight: $bold;
  line-height: 1;
  position: relative;
}

/* 丸 */
.button--forward::before {
  content: "";
  aspect-ratio: 1 / 1;
  background-color: $blue;
  border-radius: 50%;
  position: absolute;
  transition: background-color 0.3s ease;
  width: rem(38);

  @include mq(md) {
    width: rem(28);
  }
}

/* 三角 */
.button--forward::after {
  content: '';
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 8px solid $white;
  position: absolute;
  height: 0;
  transition: border-left-color 0.3s ease;
  width: 0;

  @include mq(md) {
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-left: 6px solid $white;
  }
}

.button--forward::before,
.button--forward::after {
  position: absolute;
  transition: transform .25s ease;
  transform: translateX(0);
  will-change: transform;
}

.button--forward:hover::before,
.button--forward:hover::after {
  transform: translateX(rem(6));
}

.button--search {
  border: 1px solid transparent;
  transition: border-color 0.3s ease;
}

.button--search::before {
  content: "";
  aspect-ratio: 211 / 37;
  background-image: url(../images/common/blue_mayasearch.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  height: auto;
  max-width: rem(211);
  transition: transform 0.3s ease;
  width: 100%;
}

.button--search:hover {
  background-color: #003188;
  border-color: $white;
}

.button--search:hover::before {
  background-image: url(../images/common/white_mayasearch.svg);
}
