@use "global" as *;

.error {
  overflow: hidden;
}

.error__inner {
  position: relative;
}

.error .section-title__jp {
  color: $white;
}

.error__bg.bg-image--wave {
  top: -20%;
  left: -24%;
  z-index: -1;

  @include mq(md) {
    left: 0;
  }
}

.error__text {
  color: $white;
  line-height: calc(32/16);
  margin: rem(80) auto 0;
  max-width: rem(704);
  text-align: center;
  width: 100%;

  @include mq(md) {
    margin-top: rem(40);
  }
}

.error__button {
  margin-top: rem(64);
  text-align: center;

  @include mq(md) {
    margin-top: rem(40);
  }
}

.error__button .button {
  line-height: calc(32 / 24);
  max-width: rem(337);
  padding: rem(15);
}

.error__button .button--forward::before {
  background-color: $white;
  top: 20%;
  right: 8%;

  @include mq(md) {
    right: 10%;
    top: 29%;
  }
}

.error__button .button--forward::after {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 8px solid $black;
  top: 37%;
  right: 12%;

  @include mq(md) {
    top: 39%;
    right: 12.5%;
  }
}
