@use "global" as *;


.visual-stack {
  position: relative;
  width: 100%;
}

.visual-stack figure {
  border-radius: 20px;
  overflow: hidden;
}

.visual-stack figure.visual-stack__sub {
  @include mq(md) {
    border-radius: 14px;
  }
}

.visual-stack__main {
  aspect-ratio: 4 / 3;
}

.visual-stack__sub {
  position: absolute;
  z-index: 1;
}

.visual-stack__main img,
.visual-stack__sub img {
  display: block;
  height: auto;
  object-fit: cover;
  width: 100%;
}
