@use "global" as *;

.message {
  background-color: $bg-gray;
  overflow: hidden;
  position: relative;
}

.message__curve.bg-curve {
  background-image: url(../images/common/blue_curve.webp);
}

.message__anchor {
  display: block;
  height: 0;
  pointer-events: none;
  position: absolute;
  top: calc(-1 * var(--arch-overlap-pc, rem(-10)));
  right: 0;
  left: 0;
  scroll-margin-top: var(--header-h, 0px);
}

.message__inner {
  margin-top: rem(182);

  @include mq(md) {
    margin-top: rem(80);
    width: 85%;
  }
  @include mq(xs) {
    width: 100%;
  }
}

.message__header {
  position: relative;
  z-index: 0;
}

.message .bg-image--color-wave {
  height: rem(164);
  left: 50%;
  transform: translateX(-50%);
  width: clamp(300px, calc(5.36px + 78.57vw), 608px);

  @include mq(md) {
    height: rem(80);
  }
}

.message .bg-image--color-wave::before,
.message .bg-image--color-wave::after {
  top: -18%;

  @include mq(lg) {
    top: -13%;
  }
  @include mq(md) {
    top: 14%;
  }
  @include mq(xs) {
    top: 6%;
  }
}

.message .bg-image--color-wave::before {
  left: -72%;
  right: auto;

  @include mq(lg) {
    left: -70%;
  }
  @include mq(md) {
    left: -60%;
  }
}

.message .bg-image--color-wave::after {
  right: -72%;
  left: auto;

  @include mq(lg) {
    right: -67%;
  }
  @include mq(md) {
    right: -61%;
  }
}

.message__title {
  position: relative;
  z-index: 1;
}

.message__wrapper {
  display: flow-root;
  margin-top: rem(53);
  position: relative;
  z-index: 2;

  @include mq(md) {
    margin-top: rem(60);
  }
}

.message__image {
  aspect-ratio: 471 / 508;
  border-radius: 20px;
  display: block;
  float: right;
  height: auto;
  margin-bottom: rem(60);
  margin-left: calc((40 / 1366) * 100vw);
  overflow: hidden;
  width: clamp(rem(300), calc(100% - rem(360) - rem(40)), rem(471));


  @include mq(md) {
    float: none;
    margin: rem(40) auto 0;
    width: auto;
  }
}

.message__image img {
  display: block;
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.message__content {
  margin-top: rem(18);

  @include mq(md) {
    margin: rem(24) auto;
  }
}

.message__message {
  background-color: $white;
  background-clip: padding-box;
  border-radius: 2px;
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
  box-shadow: 2px 2px 6px rgba($color: #000000, $alpha: 0.08);
  display: inline;
  font-weight: $bold;
  font-size: rem(32);
  line-break: strict;
  line-height: 2;
  padding-inline: rem(16);

  @include mq(md) {
    font-size: rem(30);
    padding-inline: rem(8);
  }
}

.message__punct {
  letter-spacing: -0.5em;
}

.message__text {
  color: $l-black;
  line-height: 2;
  margin-top: rem(26);

  @include mq(md) {
    margin-top: rem(20);
  }
}

.message__name {
  margin-top: rem(82);
  text-align: right;

  @include mq(md) {
    font-size: rem(14);
    margin-top: rem(40);
  }
}
